.App {
  font-family: Avenir Light;
  text-align: center;
}

.field {
  display: flex;
}

.katara {
  height: 50px;
}

.katara,
.suki {
  display: flex;
  flex-direction: column;
}

.row {
  display: flex;
  flex-direction: row;
}

.space {
}

.katara-body,
.suki-body {
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
  /* height: 500px; */
}

.waterball {
  height: 100px;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
  position: absolute;
  left: -30%;
  /* top: 150px; */
  animation: move 2s linear forwards;
}

@-webkit-keyframes move {
  from {
    left: -30%;
  }
  to {
    left: 100%;
  }
}
